import PropTypes from "prop-types"
import React, { memo, useRef } from "react"
import { Carousel, Rate } from "antd"
import ItemWrapper from "./style"
import IconArrowLeft from "@/assets/img/svg/icon-arrow-left"
import IconArrowRight from "@/assets/img/svg/icon-arrow-right"

const RoomItem = memo((props) => {
  const { itemData, itemWidth = "25%", itemClick = null } = props
  const CarouselRef = useRef()

  function btnClick(e, isRight = true) {
    e.stopPropagation()
    isRight ? CarouselRef.current.next() : CarouselRef.current.prev()
  }

  function itemClickHandle() {
    if (itemClick) itemClick(itemData)
  }

  const picElement = (
    <div className="cover">
      <img src={itemData.picture_url} alt="room" />
    </div>
  )

  const sliderElement = (
    <div className="slider">
      <div className="control">
        <div className="btn left" onClick={(e) => btnClick(e, false)}>
          <IconArrowLeft size="30" />
        </div>
        <div className="btn right" onClick={(e) => btnClick(e, true)}>
          <IconArrowRight size="30" />
        </div>
      </div>
      <Carousel dots={false} ref={CarouselRef}>
        {itemData?.picture_urls?.map((item) => (
          <div className="cover" key={item}>
            <img src={item} alt="room" />
          </div>
        ))}
      </Carousel>
    </div>
  )

  return (
    <ItemWrapper
      verifyColor={itemData?.verify_info?.text_color || "#39576a"}
      itemWidth={itemWidth}
    >
      <div className="inner" onClick={itemClickHandle}>
        {itemData.picture_urls ? sliderElement : picElement}
        <div className="desc">{itemData.verify_info.messages.join(" · ")}</div>
        <div className="name">{itemData.name}</div>
        <div className="price">{itemData.price_format}/晚</div>
        <div className="bottom">
          <Rate
            className="star"
            disabled
            allowHalf
            value={itemData.star_rating ?? 5}
          />
          <span className="count">{itemData.reviews_count}</span>
          {itemData.bottom_info?.content && (
            <span className="extra">{"·" + itemData.bottom_info?.content}</span>
          )}
        </div>
      </div>
    </ItemWrapper>
  )
})

RoomItem.propTypes = {
  itemData: PropTypes.object,
  itemWidth: PropTypes.string
}

export default RoomItem
