import React, { memo, useEffect, useRef, useState } from "react"
import IconArrowLeft from "@/assets/img/svg/icon-arrow-left"
import IconArrowRight from "@/assets/img/svg/icon-arrow-right"
import { ScrollViewWrapper } from "./style"

const ScrollView = memo((props) => {
  const [showRight, setShowRight] = useState(false)
  const [showLeft, setShowLeft] = useState(false)
  const [posIndex, setPosIndex] = useState(0)
  const totalDistanceRef = useRef()

  // 组件渲染完毕 判断是否显示右侧的按钮
  const scrollContentRef = useRef()
  useEffect(() => {
    const scrollWidth = scrollContentRef.current.scrollWidth // 可滚动的宽度
    const clientWidth = scrollContentRef.current.clientWidth // 本身占据的宽度
    const totalDistance = scrollWidth - clientWidth
    totalDistanceRef.current = totalDistance
    setShowRight(totalDistance > 0)
  }, [props.children])

  // 事件处理
  function clickHandle(isRight = true) {
    const newIndex = posIndex + (isRight ? 1 : -1)
    const newEl = scrollContentRef.current.children[newIndex]
    // console.log(newEl.offsetLeft)
    scrollContentRef.current.style.transform = `translate(-${newEl.offsetLeft}px)`
    setPosIndex(newIndex)

    // 判断是否继续显示左右按钮
    setShowRight(totalDistanceRef.current > newEl.offsetLeft)
    setShowLeft(newEl.offsetLeft > 0)
  }

  return (
    <ScrollViewWrapper>
      {showLeft && (
        <div className="control left" onClick={(e) => clickHandle(false)}>
          <IconArrowLeft />
        </div>
      )}
      {showRight && (
        <div className="control right" onClick={clickHandle}>
          <IconArrowRight />
        </div>
      )}
      <div className="scroll">
        <div className="scroll-content" ref={scrollContentRef}>
          {props.children}
        </div>
      </div>
    </ScrollViewWrapper>
  )
})

export default ScrollView
