import styled from "styled-components"

export const SectionFooterWrapper = styled.div`
  display: flex;
  margin-top: 10px;

  .info {
    display: flex;
    align-items: center;
    color: ${(props) => (props.hasName ? "#00848a" : "#000")};
    font-size: 17px;
    font-weight: 700;
    cursor: pointer;

    &:hover {
      text-decoration: underline;
    }

    .text {
      margin-right: 5px;
    }
  }
`
