import React, { memo, useEffect } from "react"
import { shallowEqual, useDispatch, useSelector } from "react-redux"

import HomeBanner from "./components/HomeBanner"
import { HomeWrapper } from "./style"
import { fetchHomeDataAction } from "@/store/modules/home"
import HomeSectionV1 from "./components/HomeSection-v1"
import HomeSectionV2 from "./components/HomeSection-v2"
import { isEmpty } from "@/utils"
import HomeLongfor from "./components/Home-longfor"
import HomeSectionV3 from "./components/HomeSection-v3"
import { changeHeaderConfigAction } from "../../store/modules/main"

const Home = memo(() => {
  // 获取redux中的数据
  const {
    goodPriceInfo,
    highScoreInfo,
    discountInfo,
    hotRecommendInfo,
    longforInfo,
    plusInfo
  } = useSelector(
    (state) => ({
      goodPriceInfo: state.home.goodPriceInfo,
      highScoreInfo: state.home.highScoreInfo,
      discountInfo: state.home.discountInfo,
      hotRecommendInfo: state.home.hotRecommendInfo,
      longforInfo: state.home.longForInfo,
      plusInfo: state.home.plusInfo
    }),
    shallowEqual
  )
  // 网络请求(派发异步事件)
  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(changeHeaderConfigAction({ isFixed: true }))
    dispatch(fetchHomeDataAction())
  }, [dispatch])

  return (
    <HomeWrapper>
      <HomeBanner />
      <div className="content">
        {/* 折扣 */}
        {isEmpty(discountInfo) && <HomeSectionV2 info={discountInfo} />}

        {/* 热门推荐 */}
        {isEmpty(hotRecommendInfo) && <HomeSectionV2 info={hotRecommendInfo} />}

        {/* 向往推荐 */}
        {isEmpty(longforInfo) && <HomeLongfor info={longforInfo} />}

        {/* 高性价比 */}
        {isEmpty(goodPriceInfo) && <HomeSectionV1 info={goodPriceInfo} />}

        {/* 高评分 */}
        {isEmpty(highScoreInfo) && <HomeSectionV1 info={highScoreInfo} />}

        {/* plus */}
        {isEmpty(plusInfo) && <HomeSectionV3 info={plusInfo} />}
      </div>
    </HomeWrapper>
  )
})

export default Home
