import PropTypes from "prop-types"
import React, { memo, useCallback, useState } from "react"
import SectionHeader from "@/components/section-header"
import SectionTabs from "@/components/section-tabs"
import SectionRooms from "@/components/section-rooms"
import { HomeSectionV2Wrapper } from "./style"
import SectionFooter from "@/components/section-footer"

const HomeSectionV2 = memo((props) => {
  const { info } = props

  const tabNames = info.dest_address?.map((item) => item.name)
  // useState只在组件第一次渲染时有效,在props有值时才渲染(见首页)
  const [name, setName] = useState(tabNames?.[0])
  const tabClickHandle = useCallback((index, name) => {
    setName(name)
  }, [])
  return (
    <HomeSectionV2Wrapper>
      <SectionHeader title={info.title} subtitle={info.subtitle} />
      <SectionTabs tabNames={tabNames} tabClick={tabClickHandle} />
      <SectionRooms roomList={info.dest_list?.[name]} itemWidth="33.33%" />
      <SectionFooter name={name} />
    </HomeSectionV2Wrapper>
  )
})

HomeSectionV2.propTypes = {
  info: PropTypes.object
}

export default HomeSectionV2
